.Navigation-logo {
  height: 80px;
  cursor: pointer;
}

.Navigation-logo:hover {
  -webkit-animation: Navigation-logo-spin infinite 20s linear;
          animation: Navigation-logo-spin infinite 20s linear;
}

.Navigation-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.Navigation-title {
  font-size: 1.5em;
  margin-top: 5px;
  margin-bottom: 5px;
}

.Navigation-intro {
  font-size: large;
}

@-webkit-keyframes Navigation-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Navigation-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


